* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
}

.app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.header {
  text-align: center;
  background: #fff;
  border-bottom: 1px solid #e5e5e5;
  padding: 4rem 6rem 3rem;
}

.site-title {
  letter-spacing: .02em;
  color: #000;
  margin-bottom: 0;
  font-size: 3rem;
  font-weight: 200;
}

.site-subtitle {
  color: #8b7355;
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 300;
}

.main-content {
  flex: 1;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem 6rem;
}

.filter-controls {
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 8px;
  margin-bottom: 3rem;
  padding: 2rem;
  box-shadow: 0 2px 10px #0000000a;
}

.filter-section h3 {
  color: #000;
  letter-spacing: .01em;
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
  font-weight: 500;
}

.filter-group {
  vertical-align: top;
  margin-bottom: 1rem;
  margin-right: 2rem;
  display: inline-block;
}

.filter-group label {
  color: #666;
  letter-spacing: .01em;
  margin-bottom: .5rem;
  font-size: .9rem;
  font-weight: 500;
  display: block;
}

.filter-group select {
  color: #333;
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  min-width: 140px;
  padding: .7rem 1rem;
  font-family: inherit;
  font-size: .9rem;
  transition: all .2s;
}

.filter-group select:focus {
  border-color: #000;
  outline: none;
  box-shadow: 0 0 0 2px #0000001a;
}

.clear-filters {
  color: #fff;
  cursor: pointer;
  letter-spacing: .01em;
  background: #000;
  border: none;
  border-radius: 6px;
  margin-left: 1rem;
  padding: .75rem 1.5rem;
  font-family: inherit;
  font-size: .9rem;
  font-weight: 500;
  transition: all .2s;
}

.clear-filters:hover {
  background: #333;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #00000026;
}

.results-info {
  text-align: center;
  margin-bottom: 2rem;
}

.results-info p {
  color: #666;
  font-size: .95rem;
}

.pottery-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-bottom: 3rem;
  display: grid;
}

.pottery-card {
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 2px 10px #00000014;
}

.pottery-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px #00000026;
}

.image-container {
  background: #f8f8f8;
  width: 100%;
  height: 280px;
  position: relative;
  overflow: hidden;
}

.image-container img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform .3s;
}

.pottery-card:hover .image-container img {
  transform: scale(1.05);
}

.card-content {
  padding: 1.5rem;
}

.piece-title {
  color: #000;
  letter-spacing: .01em;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.metadata {
  flex-wrap: wrap;
  gap: .5rem;
  display: flex;
}

.metadata-item {
  letter-spacing: .01em;
  border-radius: 4px;
  padding: .3rem .7rem;
  font-size: .8rem;
  font-weight: 500;
  display: inline-block;
}

.metadata-item.type {
  color: #333;
  background: #f0f0f0;
}

.metadata-item.clay {
  color: #333;
  background: #e8e8e8;
}

.metadata-item.season {
  color: #333;
  background: #f5f5f5;
}

.no-results {
  text-align: center;
  color: #8b7355;
  padding: 4rem 2rem;
}

.no-results p {
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-style: italic;
}

.no-results button {
  color: #fff;
  cursor: pointer;
  letter-spacing: .02em;
  background: linear-gradient(135deg, #8b7355 0%, #a0926f 100%);
  border: none;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  transition: all .3s;
}

.no-results button:hover {
  background: linear-gradient(135deg, #7a6449 0%, #8f8160 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #8b73554d;
}

.footer {
  text-align: center;
  color: #666;
  background: #fff;
  border-top: 1px solid #e5e5e5;
  padding: 2rem 6rem;
  font-size: .9rem;
}

@media (width <= 1024px) {
  .pottery-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 768px) {
  .site-title {
    font-size: 2.5rem;
  }

  .header {
    padding: 3rem 3rem 2rem;
  }

  .main-content {
    padding: 1rem 3rem;
  }

  .footer {
    padding: 2rem 3rem;
  }

  .filter-group {
    margin-bottom: 1.5rem;
    margin-right: 0;
    display: block;
  }

  .clear-filters {
    width: 100%;
    margin-top: 1rem;
    margin-left: 0;
  }

  .pottery-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .filter-controls {
    padding: 1.5rem;
  }
}

@media (width <= 480px) {
  .pottery-grid {
    grid-template-columns: 1fr;
  }

  .site-title {
    font-size: 2rem;
  }

  .header {
    padding: 2rem 1.5rem;
  }

  .main-content {
    padding: 1rem 1.5rem;
  }

  .footer {
    padding: 2rem 1.5rem;
  }
}
